import React from 'react';
import './App.css';
import Footer from './components/Footer/Footer';
import Section from './components/Section';
import 'bootstrap/dist/css/bootstrap.min.css';
import Sidebar from './components/Sidebar/Sidebar';
import dummyText from "./DummyText";
import SideBar from './components/Sidebar/SidebarToggle'
import SideBarToggle from './components/Sidebar/SidebarToggle';

class App extends React.Component {
  
  state = {
    sideBarOpen: true
  }

  render() {
    return (
      <div className="App">
        {/* <Header /> */}
        <SideBarToggle />
        <Sidebar />
        <Section 
          id="home" 
          text={dummyText}
          dark={true}
          title="Home"
        />
        <Section 
          id="about" 
          text={dummyText}
          dark={false}
          title="about"
        />
        <Section 
          id="experience" 
          text={dummyText}
          dark={true}
          title="experience"
        />
        <Section 
          id="projects"  
          text={dummyText}
          dark={false}
          title="projects" 
        />
        <Section 
          id="extras" 
          text={dummyText}
          dark={true}
          title="extras"
        />
        <Footer />
      </div>
    );
  }
}

export default App;